export default {
	data() {
		return {
			modalName: null,
			title: '网吧网管 上网不要钱 通宵班晚上11点，到第二天早上7点',
			price: '15',
			needNumber: '5',
			tags: [{
					tag: '有提成'
				},
				{
					tag: '交通补助'
				},
				{
					tag: '包餐食'
				},
			],
			text: '1.对餐饮加盟连锁管理和餐饮连锁运营管理有一定的了解与认识. </br>2.对餐饮加盟连锁管理和餐饮连锁运营管理有一定的了解与认识. </br>',
			companyName: '网鱼网咖有限公司'
		}
	},
	onLoad() {

	},
	methods: {
		backPage(e) {
			uni.navigateBack({
				delta: 1
			});
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		openMyform(e) {
			uni.navigateTo({
				url: '../mine/myform',
			})
		},
		goMap(e) {
			uni.navigateTo({
				url: '../../map/map',
			})
		},
		share(e) {
			uni.share({
				provider: "weixin",
				scene: "WXSceneSession",
				type: 0,
				href: "http://uniapp.dcloud.io/",
				title: "uni-app分享",
				summary: "我正在使用HBuilderX开发uni-app，赶紧跟我一起来体验！",
				imageUrl: "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/uni@2x.png",
				success: function(res) {
					console.log("success:" + JSON.stringify(res));
				},
				fail: function(err) {
					console.log("fail:" + JSON.stringify(err));
				}
			});
		},

	}


}
